<template>
  <div class="loginbox" v-if="isShow">
    <div class="login">
      <h2>
        <span>docker-network 管理</span>
      </h2>
      <form class="form">
        <h3 class="h3"> {{ $t('Regist_accountRegister')}} </h3>
        <p class="title"> {{ $t('Regist_accountRegister')}}：</p>
        <p>
          <input @keyup.13="login" type="text" v-model.trim="username" />
        </p>
        <p class="title"> {{ $t('Regist_password')}} ：</p>
        <p>
          <input @keyup.13="login" autocomplete="off" type="password" v-model.trim="password" />
        </p>
        <p class="title"> {{ $t('Regist_confirmPassword')}} ：</p>
        <p>
          <input @keyup.13="login" autocomplete="off"  type="password" v-model.trim="password1" />
        </p>
        <p class="regist">
          <span></span>
          <router-link tag="span" to="/Login" style="cursor:pointer;"> {{ $t('Regist_accountAlready')}}{{ $t('Regist_loginon')}}？  </router-link>
        </p>
        <p class="submit">
          <button @click.prevent.stop="login"> {{ $t('Regist_regist')}} </button>
        </p>
      </form>
    </div>

    <div class="bottom">
      <myFooter />
    </div>
  </div>
</template>

<script>
import http from '@/utils/http'
export default {
  data () {
    return {
      username: '',
      password: '',
      password1: '',
      isShow: 1
    }
  },
  // mounted () {
  //   if (!localStorage.getItem('baseURL')) {
  //     localStorage.setItem('baseURL', 'http://123.206.78.77:8081') // 开发环境
  //     // localStorage.setItem('baseURL', location.origin) // 上线环境
  //   }
  // },
  methods: {
    login () {
      if (!this.username || !this.password || !this.password1) {
        return this.$message({
          showClose: true,
          message: '请输入完整用户信息！',
          type: 'error'
        })
      }
      if (this.password !== this.password1) {
        return this.$message({
          showClose: true,
          message: this.$t('Regist_inconsistentPassword')+'！',
          type: 'error'
        })
      }
      http({
        url: '/login',
        method: 'POST',
        timeout: 2000000,
        data: { username: this.username, password: this.password }
      }).then(res => {
        if (res.data.Code === 200) {
          this.$message({
            showClose: true,
            message: this.$t('Regist_registSuccess')+'！',
            type: 'success'
          })
          const baseURL = localStorage.getItem('baseURL')
          const user = { username: this.username, token: res.Data, baseURL: baseURL }
          localStorage.setItem('user', JSON.stringify(user))
          localStorage.setItem('token', res.Data)
          localStorage.setItem('userList', JSON.stringify([user]))
          if (localStorage.getItem('user')) {
            this.$router.replace('/Hmoe')
          }
        } else {
          return this.$message({
            showClose: true,
            message: res.Msg,
            type: 'error'
          })
        }
      })
    }
  }
}
</script>
